import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  refToFormat,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, GpsDiversosAnexos } from '../../../../models/dtos/mfp/gpsDiversosAnexos';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const GpsDiversosAnexosSearchView: FC<SearchViewProps<GpsDiversosAnexos>> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'empresaMFP.id',
        value: globalParameter.empresaMFP.id,
      },
    ];
  }

  const columns: ColumnsProps[] = [
    {
      dataField: 'referencia',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: (cell, row) => {
        return refToFormat(row.referencia);
      },
      sort: true,
      text: 'Referência',
    },
    {
      dataField: 'receitaBrutaEmpresa',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Valor Empresa',
    },
    {
      dataField: 'receitaBrutaAnexoIV',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Anexo IV',
    },
    {
      dataField: 'receitaBrutaAnexoV',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Anexo V',
    },
    {
      dataField: 'receitaBrutaAnexoVI',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Anexo VI',
    },
  ];

  return (
    <WindowComponent
      title="Trabalhadores em Diversos Anexos"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        searchExpressions={getSearchExpression}
        extraData={getExtraData(props, 'GpsDiversosAnexosEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default GpsDiversosAnexosSearchView;
